body,
html {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

html {
  font-size: 16px;
}

body.modal-opened {
  overflow-y: hidden;
  padding-right: 20px;
}

.wrap {
  position: relative;
  min-height: 100%;
  height: 100%;
}

.container {
  width: 100%;
  max-width: 61.25rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  position: relative;
}

.d-flex {
  display: flex;
}

.flex-direction-column {
  flex-direction: column;
}

.btn {
  display: inline-block;
  border: none;
  padding: 0.8125rem 0.9375rem;
  border-radius: 0.3125rem;
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 500;
  text-decoration: none;
  background-color: #2f3234;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  font-size: 0.8125rem;
  outline: none;
}

.bg-none {
  background: none;
}

.bg-dark-orange {
  background-color: #e45028;
}

.text-white {
  color: #fff;
}

.section {
  height: 100%;
  min-height: 40.625rem;
  display: flex;
  flex-direction: column;
}

.section-title {
  font-family: Gagalin, Helvetica, sans-serif;
  font-size: 4.5rem;
}

.input-wrap {
  margin-top: 1.1875rem;
  padding: 0 0.625rem;
  flex-basis: 0;
  flex-grow: 1;
  min-width: 0.0625rem;
}

.input-wrap label {
  margin-bottom: 0.4375rem;
  cursor: pointer;
}

.input-wrap input[type=text],
.input-wrap textarea {
  border-radius: 0.3125rem;
  border: solid 0.125rem #d1cfcb;
}

.input-wrap input[type=text]:focus,
.input-wrap textarea:focus {
  outline: none;
  border: solid 0.125rem #f9b43b;
}

.radio-wrap,
.checkbox-wrap {
  margin-top: 1.1875rem;
  padding: 0 0.625rem;
}

.radio-wrap input[type=radio],
.radio-wrap input[type=checkbox],
.checkbox-wrap input[type=radio],
.checkbox-wrap input[type=checkbox] {
  position: absolute;
  visibility: hidden;
}

.radio-wrap .radio-btn,
.radio-wrap .checkbox-btn,
.checkbox-wrap .radio-btn,
.checkbox-wrap .checkbox-btn {
  width: 0.9375rem;
  height: 0.9375rem;
  margin: auto 0.625rem auto 0;
  position: relative;
  background: #fff;
}

.radio-wrap .radio-btn,
.checkbox-wrap .radio-btn {
  border-radius: 50%;
}

.radio-wrap input[type=radio]:checked + label .radio-btn::before,
.checkbox-wrap input[type=radio]:checked + label .radio-btn::before {
  content: " ";
  display: block;
  width: 0.4375rem;
  height: 0.4375rem;
  border-radius: 50%;
  position: absolute;
  top: 0.25rem;
  left: 0.25rem;
  background-color: #e45028;
}

.radio-wrap input[type=checkbox]:checked + label .checkbox-btn::before,
.checkbox-wrap input[type=checkbox]:checked + label .checkbox-btn::before {
  content: " ";
  display: block;
  height: 0.6875rem;
  width: 0.4375rem;
  border-right: 0.1875rem solid #e45028;
  border-bottom: 0.1875rem solid #e45028;
  box-sizing: border-box;
  transform: rotate(45deg) translateX(0.1875rem) translateY(-0.1875rem);
}

.ml-1 {
  margin-left: 5%;
}

.caret-down {
  display: inline-block;
  width: 0.5625rem;
  height: 0.4375rem;
  border-style: solid;
  border-width: 0.4375rem 0.28125rem 0 0.28125rem;
  border-color: #f9b43b transparent transparent transparent;
}

.modal-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: -200vw;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  overflow-y: hidden;
  background: rgba(47, 50, 52, 0.99);
  transition: 0.3s linear all;
}

.modal-menu.modal-menu--open {
  left: 0;
}

.modal-menu__logo {
  position: absolute;
  top: 1.25rem;
  left: 0.875rem;
  width: 5.75rem;
  height: 4.3125rem;
}

.modal-menu__logo-icon {
  width: 100%;
  height: 100%;
}

.modal-menu__elem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal-menu__elem::before {
  content: "";
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  background: #fff;
  margin: 0.9375rem 0;
}

.modal-menu__elem:first-child::before {
  display: none;
}

.modal-menu__link {
  display: block;
  color: #ffffff;
  font-size: 1.5rem;
  text-decoration: none;
}

.modal-menu__close {
  position: absolute;
  top: 1.25rem;
  right: 2.125rem;
  fill: #fff;
  width: 1.25rem;
  height: 1.25rem;
  opacity: 0.7;
}

.modal-menu__close-icon {
  width: 100%;
  height: 100%;
}

.greeting {
  background: url("../images/video-bg.png") center center no-repeat;
  background-size: cover;
}

.greeting__container {
  flex: 1;
}

.header {
