}

.nav__list {
  display: flex;
}

.nav__list-item {
  display: flex;
  align-items: center;
}

.nav__list-item::before {
  content: "";
  width: 0.25rem;
  height: 0.25rem;
  border-radius: 50%;
  background: #fff;
  margin: 0 0.9375rem;
}

.nav__list-item:first-child::before {
  display: none;
}

.nav__link {
  color: #fff;
  text-decoration: none;
}

.menu-link {
  display: none;
  padding-left: 1.875rem;
}

.menu-link__rect {
  width: 1.5rem;
  height: 0.25rem;
  display: block;
  background-color: #fff;
  border-radius: 3px;
}

.menu-link__rect + .menu-link__rect {
  margin-top: 0.3125rem;
}

.greeting__content {
  flex: .8;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: LifeHack;
  font-size: 4.0625rem;
}

.greeting__text {
  margin-left: 2.8125rem;
}

.greeting__title {
  color: #f9b43b;
}

.greeting__sub-title {
  width: 25.375rem;
  letter-spacing: -0.1rem;
  margin-top: 1.25rem;
}

.greeting__images {
  position: relative;
}

.greeting__images .greeting__drops {
  position: absolute;
  top: 2.5rem;
  left: -1.875rem;
}

.greeting__img {
  max-width: 100%;
}

.scroll-down {
  position: absolute;
  bottom: 3.125rem;
  left: calc(50% - 14.5px);
  cursor: pointer;
}

.best {
  background: url("../images/bg.png") no-repeat center center;
  background-size: cover;
  justify-content: center;
}

.best__title {
  text-align: center;
}

.best__list {
  display: flex;
  text-align: center;
  margin-top: 3.4375rem;
}

.best__list-item {
  width: 17.875rem;
}

.best__list-item + .best__list-item {
  margin-left: 2.3125rem;
}

.reason__img-wrap {
  height: 5.625rem;
  display: flex;
  margin-bottom: 2.0625rem;
}

.reason__img {
  margin: auto;
}

.reason__title {
  font-family: LifeHack;
  font-size: 1.5rem;
  color: #2f3234;
  margin-bottom: 1.25rem;
}

.reason__description {
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 0.875rem;
  line-height: 1.71;
  color: rgba(47, 50, 52, 0.8);
}

.team {
  background: url("../images/team.png") center center/cover no-repeat;
  position: relative;
}

.team-block {
  position: absolute;
  right: 0;
  width: 50%;
  height: 100%;
  background: rgba(47, 50, 52, 0.93);
  padding: 5% 2% 0 7%;
  color: #fff;
  font-family: Gagalin, Helvetica, sans-serif;
  font-size: 0.875rem;
}

.team__title {
  color: #f9b43b;
  margin-bottom: 12%;
}

.team__member + .team__member {
  margin-top: 2.4375rem;
}

.team__member--active .caret-down {
  transform: rotate(180deg);
}

.team__member--active .team__member-name {
  color: #f9b43b;
}

.team__member-trigger {
  display: inline;
  cursor: pointer;
}

.team__member-name {
  display: inline-block;
  padding-left: 0.3125rem;
}

.team__member-description {
  display: flex;
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 300;
  overflow: hidden;
  padding-top: 1.0625rem;
  padding-left: 0.9375rem;
  transition: height 0.3s;
}

.team__member-avatar {
  width: 6.25rem;
  height: 6.25rem;
  background: center center / cover no-repeat;
  border-radius: 50%;
}

.team__member-text {
  flex: 1;
  padding-left: 1.3125rem;
  line-height: 1.71;
}

.team__member-post {
  font-weight: bold;
  margin-bottom: 0.625rem;
}

.burgers {
  width: 100%;
  height: 100vh;
  background: linear-gradient(to top, #f9b43b, #eb8c38 48%, #f9b43b 74%);
}

.burgers__container {
  height: 100%;
}

.slider {
  display: flex;
  height: 100%;
  position: relative;
}

.slider__btn {
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

.slider__btn--prev .slider__btn-icon {
  transform: rotate(90deg);
}

.slider__btn--next .slider__btn-icon {
  transform: rotate(-90deg);
}

.slider__slides {
  flex: 1;
}

.slider__slide {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s ease 0s;
}

.burger {
  display: flex;
  color: #fff;
}

.burger__col {
  flex-shrink: 1;
  flex-grow: 1;
  flex-basis: 0;
}

.burger__visual {
  position: relative;
  font-family: Gagalin, Helvetica, sans-serif;
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.burger__consist,
.burger__calorie {
  position: absolute;
  left: 0;
  color: #ffffff;
}

.burger__consist {
  top: 0;
  text-decoration: none;
  background-color: rgba(228, 80, 40, 0.4);
  border-radius: 5px;
  width: 5.625rem;
  height: 5.625rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.burger__consist:hover .burger__consist-list,
.burger__consist:active .burger__consist-list {
  width: 16.875rem;
}

.burger__consist-list {
  position: absolute;
  top: 0;
  left: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
  color: #000;
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 300;
  width: 0;
  overflow: hidden;
  transition: 0.3s linear all;
}

.burger__consist-item {
  padding: 5% 10% 5% 0;
  width: 16.875rem;
  display: flex;
}

.burger__consist-icon {
  flex: 1;
}

.burger__consist-text {
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.burger__consist-name {
  font-weight: bold;
}

.burger__img {
  max-width: 100%;
  max-height: 100%;
  padding: 0.3125rem;
}

.burger__calorie-wrap {
  position: relative;
}

.burger__calorie {
  bottom: 0;
  opacity: 0.5;
  text-align: center;
}

.burger__calorie-value {
  font-size: 1.25rem;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.burger__name {
  font-family: Gagalin, Helvetica, sans-serif;
  font-size: 72px;
  line-height: 0.89;
  padding-bottom: 10%;
}

.burger__description {
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 0.875rem;
  line-height: 1.71;
}

.burger__price {
  display: inline-block;
  font-family: Gagalin, Helvetica, sans-serif;
  font-size: 72px;
  line-height: 0.93;
  letter-spacing: -0.1125rem;
  color: #e45028;
  padding-right: 5%;
  padding-top: 5%;
