.burger__price-curency {
  font-size: 1.875rem;
  letter-spacing: -0.05rem;
}

.burger__order {
  vertical-align: super;
}

.menu {
  background: url("../images/menu-bg.png") 50%/cover no-repeat;
  position: relative;
}

.menu__title {
  color: #fff;
  position: absolute;
  top: 10%;
  left: 10%;
}

.menu-block {
  position: absolute;
  right: 0;
  height: 100%;
  left: 50%;
}

.menu__list {
  display: flex;
  height: 100%;
  position: relative;
  justify-content: flex-end;
}

.menu__item {
  display: flex;
  max-height: 100%;
  color: #fff;
  justify-content: flex-end;
  width: 5rem;
  transition: 0.3s all;
}

.menu__item--active {
  width: 100%;
}

.menu__item--active .menu__item-trigger {
  color: #f9b43b;
}

.menu__item--active .menu__item-description {
  overflow: auto;
  padding: 3.75rem 1.875rem;
}

.menu__item-trigger {
  flex: none;
  background: center center / auto 100% no-repeat;
  font-family: Gagalin, Helvetica, sans-serif;
  color: #fff;
  text-decoration: none;
  width: 5rem;
  height: 100%;
  float: left;
  position: relative;
}

.menu__item-trigger::before {
  content: " ";
  display: block;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
}

.menu__trigger-text {
  font-size: 2.5rem;
  white-space: nowrap;
  position: absolute;
  transform: rotate(-90deg) translateY(-50%);
  transform-origin: 0 0;
  left: 50%;
  margin-top: -1.25rem;
}

.menu__item-description {
  overflow: hidden;
  background-color: rgba(47, 50, 52, 0.93);
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 0.875rem;
  line-height: 1.8;
  transition: 0.3s width;
}

.reviews {
