  opacity: 0;
  visibility: hidden;
  transition: 0.3s linear all;
}

.reviews__reviewer:hover .reviews__review,
.reviews__reviewer:active .reviews__review {
  opacity: 1;
  visibility: visible;
}

.reviews__review-author {
  font-family: Gagalin, Helvetica, sans-serif;
  margin-bottom: 1.875rem;
}

.reviews__review-text {
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 300;
  line-height: 1.71;
  margin-bottom: 1.0625rem;
}

.reviews__review-link--mobile {
