}

.player--active .player__pause {
  display: block;
}

.player--active .player__start {
  display: none;
}

.player--active .player__controls {
  opacity: 0;
}

.player__wrapper {
  position: relative;
}

.player__splash-screen {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: url("../images/work.png") center center/cover no-repeat;
}

.player__controls {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  flex-direction: column;
  display: flex;
  transition: 0.6s 4s linear opacity;
}

.player__controls:hover {
  opacity: 1;
  transition-delay: 0s;
}

.player__screen-btns {
  flex: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.player__duration-panel {
  height: 2.1875rem;
  background-color: #2f3234;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0.125rem 0;
}

.player__btn {
  background: none;
  outline: none;
  border: none;
  opacity: 0.8;
  cursor: pointer;
}

.player__pause {
  display: none;
}

.player__btn-icon {
  fill: #f5f2ed;
  margin: auto;
}

.player__scale {
  position: relative;
  cursor: pointer;
  padding: 4px 0;
}

.player__scale-line {
  width: 100%;
  height: 0.25rem;
  border-radius: 1px;
  background-color: #626262;
}

.player__scale-circle {
  height: 0.75rem;
  width: 0.75rem;
  border-radius: 50%;
  background-color: #626262;
  position: absolute;
  top: 0;
  right: 0;
}

.player__time-scale {
  width: 70%;
}

.player__volume {
  margin-left: 3%;
  flex: 1;
  display: flex;
  align-items: center;
}

.player__volume-scale {
  width: 50%;
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0.6875rem 0;
  background-color: #2f3234;
}

.footer__container {
  flex-direction: row;
}

.footer__socials {
  margin-bottom: 0;
  align-items: flex-end;
}

.footer__info,
.footer__socials {
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: center;
}

.footer__address {
  color: #fff;
  font-size: 0.875rem;
  margin-bottom: 0.625rem;
}

.footer__phone {
  color: #e45028;
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: bold;
  text-decoration: none;
  font-size: 0.875rem;
}

.socials {
  display: flex;
  align-items: center;
}

.socials__item {
  margin-left: 0.625rem;
}

.socials__link {
  display: inline-block;
  max-width: 1.4375rem;
  max-height: 1.25rem;
  opacity: 0.3;
  transition: 0.3s;
}

.socials__link:hover {
  opacity: 1;
}

.socials__link-icon {
  width: 1.25rem;
  height: 1.25rem;
  fill: #fff;
}

.section-paginator {
  position: fixed;
  right: 3.125rem;
  top: 5.6875rem;
}

.section-paginator .paginator__list-item {
  display: flex;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  margin: 0.46875rem 0;
}

.section-paginator .paginator__list-item.active {
  border: 0.0625rem solid #fff;
}

.section-paginator .paginator__link {
  display: block;
  width: 0.375rem;
  height: 0.375rem;
  background-color: #fff;
  border-radius: 50%;
  margin: auto;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  transition: 0.3s all;
  z-index: -1;
}

.popup--show {
  opacity: 1;
  z-index: 100;
}

.popup__wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.popup__background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(47, 50, 52, 0.92);
}

.popup__content {
  max-width: 80%;
  z-index: 101;
  margin: auto;
  padding: 2%;
  background-color: #ffffff;
  border-radius: 0.375rem;
  box-shadow: 0 0 40px rgba(0, 0, 1, 0.5);
  text-align: center;
}

.popup__text {
  margin-bottom: 1.0625rem;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  transition: 0.3s all;
  z-index: -1;
}

.modal--show {
  opacity: 1;
  z-index: 100;
}

.modal__wrapper {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.modal__background {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: rgba(47, 50, 52, 0.92);
}

.modal__content {
  max-width: 30%;
  z-index: 101;
  margin: auto;
  padding: 2%;
  background-color: #ffffff;
  border-radius: 0.375rem;
  box-shadow: 0 0 40px rgba(0, 0, 1, 0.5);
  color: #2f3234;
}

.modal__header {
  display: flex;
  justify-content: space-between;
  font-family: Gagalin, Helvetica, sans-serif;
}

.modal__close {
  width: 0.9375rem;
  height: 0.9375rem;
  background: none;
  padding: 0;
}

.modal__close-icon {
  width: 100%;
  height: 100%;
  fill: #e45028;
}

.modal__body {
  font-family: Ubuntu, Helvetica, sans-serif;
  font-weight: 300;
  margin-top: 1.875rem;
}

@media screen and (max-width: 768px) {
  html {
    font-size: 14px;
  }

  .container {
    width: 95%;
  }

  .section {
    min-height: initial;
  }

  .nav__list {
    display: none;
  }

  .menu-link {
    display: block;
  }

  .greeting__content {
    flex-direction: column;
    padding: 0 12%;
  }

  .greeting__text {
    margin-left: 0;
    text-align: center;
  }

  .greeting__title {
    font-size: 3.9375rem;
  }

  .greeting__sub-title {
    font-size: 3.375rem;
  }

  .team-block {
    width: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 0 15%;
  }

  .team__member-description {
    padding-left: 0;
    align-items: center;
    flex-direction: column;
  }

  .team__member-text {
    padding-left: 0;
  }

  .burger {
    flex-wrap: wrap;
  }

  .burger__col {
    flex-basis: 100%;
  }

  .burger__calorie {
    right: 0;
    top: 0;
    bottom: auto;
    left: auto;
  }

  .burger__text {
    text-align: center;
  }

  .burger__name {
    padding: 5%;
  }

  .menu-block {
    left: 0;
  }

  .menu__item {
    width: 4.0625rem;
  }

  .menu__item-trigger {
    width: 4.0625rem;
  }

  .reviews__reviewer {
    width: 50%;
    height: 25%;
  }

  .reviews__review {
    padding: 1.875rem 3.375rem 0 3.4375rem;
    text-align: center;
  }

  .reviews__review-text {
    margin-bottom: 1.6875rem;
  }

  .order__form {
    flex-direction: column;
  }

  .order__col {
    width: 100%;
    padding: 0 15%;
  }

  .section-paginator {
    display: none;
  }

  .modal__content {
    max-width: 60%;
  }
}

@media screen and (max-width: 480px) {
  html {
    font-size: 13px;
  }

  .d-mobile-none {
    display: none;
  }

  .best__list {
    flex-direction: column;
  }

  .best__list-item + .best__list-item {
    margin: 0;
  }

  .best__list-item {
    width: 100%;
  }

  .reason {
    display: flex;
  }

  .reason__img-wrap {
    width: 25%;
  }

  .reason__text {
    text-align: left;
    width: 75%;
    padding-left: 5%;
  }

  .slider__btn {
    position: absolute;
    bottom: 0;
    top: 0;
  }

  .slider__btn--prev {
    left: 0;
  }

  .slider__btn--next {
    right: 0;
  }

  .burger__name {
    font-size: 3.125rem;
    line-height: 1.06;
  }

  .menu__item {
    width: 3.125rem;
  }

  .menu__item--active {
    position: absolute;
    top: 0;
    bottom: 0;
    z-index: 100;
  }

  .menu__item-trigger {
    width: 3.125rem;
  }

  .menu__trigger-text {
    font-size: 1.875rem;
  }

  .reviews__review {
    padding: 15%;
  }

  .reviews__review-author {
    font-size: 1.5rem;
  }

  .reviews__review-text {
    display: none;
  }

  .reviews__review-link {
    display: none;
  }

  .reviews__review-link--mobile {
    display: inline-block;
  }

  .order__col {
    padding: 0 5%;
  }

  .order__row {
    justify-content: center;
  }

  .footer__container {
    flex-direction: column;
    justify-content: center;
  }

  .footer__info,
  .footer__socials {
    width: 100%;
    align-items: center;
  }

  .footer__phone {
    margin-bottom: 5%;
  }

  .modal__content {
    max-width: 90%;
  }
}