/* LifeHack font */
@font-face {
    font-family: LifeHack;
    src: url('../fonts/Lifehack\ Medium.otf');
}

/* Gagalin font */
@font-face {
    font-family: Gagalin;
    src: url('../fonts/Gagalin/Gagalin-Regular.otf');
}

/* Ubuntu fonts */
@font-face {
    font-family: Ubuntu;
    font-weight: bold;
    src: url('../fonts/Ubuntu/Ubuntu-B.ttf');
}

@font-face {
    font-family: Ubuntu;
    font-weight: 500;
    src: url('../fonts/Ubuntu/Ubuntu-Medium.ttf');
}

@font-face {
    font-family: Ubuntu;
    font-weight: 300;
    src: url('../fonts/Ubuntu/Ubuntu-Light.ttf');
}

@font-face {
    font-family: Ubuntu;
    src: url('../fonts/Ubuntu/Ubuntu-Regular.ttf');
}

@mixin gagalin() {
    font-family: Gagalin, Helvetica, sans-serif;
}

@mixin ubuntu($style: light) {
    font-family: Ubuntu, Helvetica, sans-serif;

    @if $style==light {
        font-weight: 300;
    }

    @if $style==medium {
        font-weight: 500;
    }

    @if $style==bold {
        font-weight: bold;
    }
}

@mixin lifehack() {
    font-family: LifeHack;
}