.team {
    background: url('../images/team.png') center center / cover no-repeat;
    position: relative;
}

.team-block {
    position: absolute;
    right: 0;
    width: 50%;
    height: 100%;
    background: rgba($dark-grey, 0.93);
    padding: 5% 2% 0 7%;
    color: #fff;
    @include gagalin;
    font-size: rem(14px);

    @include tablets {
        width: 100%;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 0 15%;
    }
}

.team__title {
    color: $yellow;
    margin-bottom: 12%;
}

.team__member {
    &+.team__member {
        margin-top: rem(39px);
    }

    &--active {
        .caret-down {
            transform: rotate(180deg);
        }

        .team__member-name {
            color: $yellow;
        }
    }
}

.team__member-trigger {
    display: inline;
    cursor: pointer;
}

.team__member-name {
    display: inline-block;
    padding-left: rem(5px);
}

.team__member-description {
    display: flex;

    @include ubuntu(light);
    overflow: hidden;
    padding-top: rem(17px);
    padding-left: rem(15px);
    transition: height 0.3s;

    @include tablets {
        padding-left: 0;
        align-items: center;
        flex-direction: column;
    }
}

.team__member-avatar {
    width: rem(100px);
    height: rem(100px);
    background: center center / cover no-repeat;
    border-radius: 50%;
}

.team__member-text {
    flex: 1;
    padding-left: rem(21px);
    line-height: 1.71;

    @include tablets {
        padding-left: 0;
    }
}

.team__member-post {
    font-weight: bold;
    margin-bottom: rem(10px);
}