.socials {
    display: flex;
    align-items: center;
}

.socials__item {
    margin-left: rem(10px);
}

.socials__link {
    display: inline-block;
    max-width: rem(23px);
    max-height: rem(20px);
    opacity: 0.3;
    transition: 0.3s;
    &:hover {
        opacity: 1;
    }
}

.socials__link-icon {
    width: rem(20px);
    height: rem(20px);
    fill: #fff;
}